var globalVar=require('./core/global-variable.js');//常用标志变量
require("./core/main-nav.js");
var Konva=require("konva");
var drawImg=require('./core/draw-img.js');
fillPage();
//内容响应
function fillPage(){
    var funVideoO=$('#funVideo');
    var funGameClubO=$("#funGameClub");
    var gameClubBannerImgO=$("#gameClubBannerImg");
    //如果是支持canvas的则用Canvas
    if(globalVar.canvas){
        if((globalVar.mobile && globalVar.winW<=780) || globalVar.winW<=780){
            drawMobileCanvas();
        }else{
            drawCanvas();
        }
    }else{//不支持canvas的就直接用图片替代位置
        replaceHtml(gameClubBannerImgO);
    }
    //如果是移动端或者浏览器不支持视频就执行视频dom插入
    if(globalVar.mobile || !globalVar.video){
        replaceHtml(funGameClubO);
    }else{
        replaceHtml(funVideoO);
    }
}
//PC端绘制canvas
function drawCanvas(){
    var animItem=[];
    var stage=new Konva.Stage({
        container:'gameClubCanvas',
        width:1600,
        height:700
    });
    //建立canvas层
    var layer=new Konva.Layer();
    //开始绘制元素
    drawImg.addImg('../images/gameClub/game-club-banner-canvas.png',0,0,1600,700,stage,layer,function(){
        //生日关怀
        drawImg.addImg('../images/gameClub/birthday-icon-pc.png',563,102,119,30,stage,layer,function(obj,layer){
            var midx=489,midy=117,rotateR=18,lineo=null,oMidx=688,oMidy=206,oR=3,diffX=-96,diffY=-15;
            drawImg.drawDot(oMidx+oR,oMidy+oR,oR,'#f5cd95',stage,layer);//绘制圆点
            circleMove(obj,midx,midy,rotateR,oMidx+oR,oMidy+oR,diffX,diffY);
        });
        //服务特权
        drawImg.addImg('../images/gameClub/server-icon-pc.png',1114,150,119,30,stage,layer,function(obj,layer){
            var midx=1129,midy=165,rotateR=10,lineo=null,oMidx=926,oMidy=248,oR=3,diffX=-15,diffY=-15;
            drawImg.drawDot(oMidx+oR,oMidy+oR,oR,'#f5cd95',stage,layer);//绘制圆点
            circleMove(obj,midx,midy,rotateR,oMidx+oR,oMidy+oR,diffX,diffY);
        });
        //游戏祝福
        drawImg.addImg('../images/gameClub/game-icon-pc.png',422,414,119,30,stage,layer,function(obj,layer){
            var midx=437,midy=429,rotateR=15,lineo=null,oMidx=680,oMidy=337,oR=3,diffX=-96,diffY=-15;
            drawImg.drawDot(oMidx+oR,oMidy+oR,oR,'#f5cd95',stage,layer);//绘制圆点
            circleMove(obj,midx,midy,rotateR,oMidx+oR,oMidy+oR,diffX,diffY);
        });
        //形象特权
        drawImg.addImg('../images/gameClub/avatar-icon-pc.png',1025,326,119,30,stage,layer,function(obj,layer){
            var midx=1040,midy=341,rotateR=26,lineo=null,oMidx=915,oMidy=317,oR=3,diffX=-15,diffY=-15;
            drawImg.drawDot(oMidx+oR,oMidy+oR,oR,'#f5cd95',stage,layer);//绘制圆点
            circleMove(obj,midx,midy,rotateR,oMidx+oR,oMidy+oR,diffX,diffY);
        });
    })
    /*
    回调函数
    @param obj是当前传入的图片或精灵对象
    @param cx是当前应该围绕旋转的中心点x坐标
    @param cy是当前应该围绕旋转的中心点y坐标
    @param cr是当前应该围绕旋转的半径值
    @param ox是相对定位的另一点x坐标
    @param oy是相对定位的另一点y坐标
    @param difx是x方向的位置差
    @param dify是y方向的位置差
    */
    function circleMove(obj,cx,cy,cr,ox,oy,difx,dify){
        var anim=null;
        var angle=0;
        var circleO={
            circleObj:obj,
            cx:cx,
            cy:cy,
            cr:cr,
            ox:ox,
            oy:oy,
            difx:difx,
            dify:dify,
            line:null
        };
        animItem.push(circleO);
        if(animItem.length==4){//只有4个icon全部加载完成才可启动动画
            anim = new Konva.Animation(function(frame) {
                angle++;
                if(angle>360){
                    angle=0;
                };
                for(var i=0,len=animItem.length;i<len;i++){
                    rotateAn(animItem[i],angle,aniBack);
                }
            }, layer);
            anim.start();
        }
    }
    //绘制直线
    function aniBack(obj,x,y){
        obj.line && obj.line.remove();
        obj.line=drawImg.drawLine([obj.ox,obj.oy,x,y],2,'#f5cd95',stage,layer);//给制连接线
        obj.circleObj.moveToTop();
        obj.circleObj.draw();
    }
}

