/*
 添加一个图片方法
 @param imgO用来加载图片的图片对象
 @param url要加载的图片的地址
 @param x图片放的位置x坐标
 @param y图片放的位置Y坐标
 @param w图片的宽度
 @param h图片高度
 */
function addImg(url,x,y,w,h,stage,layer,fn,id){
    var imgO=new Image();
    imgO.onload = function() {
        var imgObj = new Konva.Image({
            x: x ,
            y: y ,
            image: imgO,
            id: id ? id : '',
            width: w,
            height: h,
            preventDefault: false
        });
        layer.add(imgObj);
        stage.add(layer);
        fn && fn(imgObj,layer);
    };
    imgO.src = url;
};
/*
 添加一个sprite方法
 @param imgO用来加载图片的图片对象
 @param url要加载的图片的地址
 @param x图片放的位置x坐标
 @param y图片放的位置Y坐标
 @param anis动画帧数据
 */
function addSprite(url,x,y,anis,ani,stage,layer,fn,id,rate){
    var imgO=new Image();
    var spriteObj=null;
    imgO.onload = function() {
        spriteObj = new Konva.Sprite({
            x: x ,
            y: y ,
            image: imgO,
            id: id ? id : '',
            animation: ani,
            animations:anis,
            frameRate: rate ? rate : 2,
            frameIndex: 0
        });
        layer.add(spriteObj);
        stage.add(layer);
        spriteObj.start();
        fn && fn(spriteObj,layer);
    };
    imgO.src = url;
};
/*
 绘制一个纯色圆的方法
 @param x是绘画圆的x坐标
 @param y是绘画圆的x坐标
 @param r是绘画圆的半径
 @param color是绘画圆的填色
 @param stage是圆绘制到的舞台
 @param layer是圆绘制到的层
 @param dotArr是绘制的圆的存储数组
 */
function drawDot(x,y,r,color,stage,layer,dotArr){
    var circle = new Konva.Circle({
        x: x,
        y: y,
        radius: r,
        fill: color
    });
    layer.add(circle);
    stage.add(layer);
    dotArr && dotArr.push(circle);
};
/*
 绘制一条线的方法,默认返回线段本身
 @param arr是绘画线段的坐标点
 @param w是绘制的线段的粗细
 @param color是绘画线段的填色
 @param stage是圆绘制到的舞台
 @param layer是圆绘制到的层
 @param lineArr是绘制的圆的存储数组
 */
function drawLine(arr,w,color,stage,layer,lineArr){
    var line = new Konva.Line({
        points: arr,
        stroke: color,
        strokeWidth: w
    });
    layer.add(line);
    stage.add(layer);
    lineArr && lineArr.push(line);
    return line;
};
/*
 绘制文本
 @param str是要绘制的文本内容
 @param tx是文本的X坐标
 @param ty是文本的Y坐标
 @param font是{},文本的字号，颜色，字体
 @param stage是文本绘制到的舞台
 @param layer是文本绘制到到的层
 @param lineArr是绘制的文本的存储数组
 */
function drawText(str,tx,ty,font,stage,layer,lineArr){
    var simpleText = new Konva.Text({
        x: tx,
        y: ty,
        text: str,
        fontSize: font.size,
        fontFamily: font.family,
        fill: font.color
    });
    layer.add(simpleText);
    stage.add(layer);
    lineArr && lineArr.push(simpleText);
    return simpleText;
};
module.exports={
    addImg:addImg,
    addSprite:addSprite,
    drawDot:drawDot,
    drawLine:drawLine,
    drawText:drawText
};


