//导航置高亮效果
var normalFn=require('./active.js');
var activeInputO=$('#activeInput');
activeInputO && normalFn.setActive(activeInputO.val());

var mobileNavOpenBtn=$('#main-nav__btn-mobile'),
    mobileNavCon=$('#main-nav-mobile'),
    mobileNavCloseBtn=$('#main-nav-mobile__close'),
    mainNav=$('#mian-nav'),
    scrollTimer=null;//节流
//显示移动端导航
mobileNavOpenBtn.on('click',function(){
    try{
        bearcatBodyO && bearcatBodyO.stop(true);
        swordO && swordO.stop(true);
        cloakO && cloakO.stop(true);
    }catch(err){}

    // if(svgAnim){
    //     svgAnim.stop();
    // }
    mobileNavCon.addClass('active');
});
//关闭移动端导航
mobileNavCloseBtn.on('click',function(){
    mobileNavCon.removeClass('active');
    bearCatAn && bearCatAn();
});
//导航固定位置效果实现
$(window).on('scroll',function(){
    clearTimeout(scrollTimer);
    scrollTimer=setTimeout(function(){
        var scrollTop=$(document).scrollTop(),
            mainNavH=mainNav.height();
        if(scrollTop>mainNavH){
            mainNav.addClass('active');
        }else{
            mainNav.removeClass('active');
        }
    },300)
});
//底部二维码效果
var wechatQrcodeO=$('#wechatQrcode'),
    wechatQrcodeImg=wechatQrcodeO.find('.wechat-qrcode');
wechatQrcodeO.on('touchstart',function(e){
    if(!wechatQrcodeImg.is(':visible')){
        e.stopPropagation();
        wechatQrcodeImg.show();
    }
});
$(document).on('touchstart',function(){
    if(wechatQrcodeImg.is(':visible')){
        wechatQrcodeImg.hide();
    }
});
